export default function CommentsList(props) {
    const commnets = props.comments
    return (
        <div>
            <h3>Comments:</h3>
            {commnets.map((comment, i) => (
                <div className="content-link-item" key={i}>
                    <h4>{comment.username}</h4>
                    <p>{comment.text}</p>
                </div>
            ))}
        </div>
    )
}